import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'
import moment from 'moment'

import testExperienceReportDetail from '@/view/crowdTest/test-experience-report-detail.vue'; //众测报告详情页

Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'testExperienceReportDetail',
    component: testExperienceReportDetail
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



